<template>
  <div class="column-view">
    <div ref="topBar">
      <van-nav-bar
        title="我的二维码"
        left-text="返回"
        fixed
        left-arrow
        @click-left="()=>this.$router.back()">
      </van-nav-bar>
    </div>
    <div class="under-nav" style="text-align: center">
      <img :src="imgUrl" alt="" :width="imgWidth" :height="imgHeight"/>
    </div>
  </div>
</template>

<script>
  import { QRCODE_IMG_URL } from "@/config/host"

  export default {
    name: "QRCode",
    data () {
      return {
        imgWidth: 300,
        imgHeight: 300,
        imgUrl: null
      }
    },
    async mounted () {
      const result = await this.httpPost(QRCODE_IMG_URL, {
        width: this.imgWidth,
        height: this.imgHeight
      })
      if (result.success) {
        this.imgUrl = result.data
      }
    }
  }
</script>

